body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html{
  width: 100%;
  height: 100%;
}
#root{
  width: 100%;
  height: 100%;
}

.App{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: #303030;
  width: 100%;
  height: 100%;
}

.plot {
  width: 80vw;
}

.settings{
  display: flex;
  flex-direction: column;
  position: absolute;
  justify-content: center;
  width: 60vw;
  top: 10px;
  right:10px;
}

.select-group{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.title{
  color: #ddd;
  font-size: 4vw;
}

.subtitle{
  color: #ddd;
  font-size: 2.5vw;
}

.legend{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  width: 60vw;
  font-size: 2vw;
}

.legend-item{
  display: flex;
  flex-direction: row;
  color: #eee;
  align-items: center;
  width: 15vw;
}

.legend-rect{
  background-color: #000;
  width: 4vw;
}


